html,body,div,span,
applet,object,iframe,
h1,h2,h3,h4,h5,h6,p,blockquote,pre,
a,abbr,acronym,address,big,cite,code,
del,dfn,em,font,img,ins,kbd,q,s,samp,
small,strike,strong,sub,sup,tt,var,
dd,dl,dt,li,ol,ul,
fieldset,form,label,legend,select,
table,caption,tbody,tfoot,thead,tr,th,td,input,textarea,button,i,span{
	margin:0;
	padding:0;
	border:0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  font-size:12px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#eee;
}
input,button,textarea,td,th,select{outline:none;}
table {border-collapse:collapse;border-spacing:0;}
ol,ul{list-style: none;}
a,a:visited,a:active,a:link{text-decoration:none;}
.clearfix:after{
	content:'';
	display:block;
	clear:both;
}
.clearfix{
	zoom:1;
}
#app{
  background-color:#eee;
}
.container{
  margin:0 auto;
  max-width:980px;
  background-color:#fff;
}
button{
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
  padding:6px 12px;
  color:#fff;
  background-color:#07a0f8;
  border-radius: 6px;
  cursor:pointer;
}
button:hover{
  background-color:#0475f7;
}
.loading:before{
  content:'';
  position:fixed;
  z-index:100;
  top:42px;
  bottom:0;
  width:100%;
  background-color:#fff;
}
.loading:after{
  content:'';
  position:fixed;
  z-index:101;
  top:30%;
  left:50%;
  margin-left:-20px;
  -webkit-animation: loading 1s linear infinite;
          animation: loading 1s linear infinite;
  width:40px;
  height:40px;
  border:1px solid #0673f0;
  border-top-color: transparent;
  border-radius: 50%;
}
@-webkit-keyframes loading {
  from {-webkit-transform: rotate(0deg);transform: rotate(0deg)}
  to {-webkit-transform: rotate(360deg);transform: rotate(360deg)}
}
@keyframes loading {
  from {-webkit-transform: rotate(0deg);transform: rotate(0deg)}
  to {-webkit-transform: rotate(360deg);transform: rotate(360deg)}
}
.backTop{
  position:fixed;
  z-index:10;
  -webkit-transition: right .4s ease-out;
  -o-transition: right .4s ease-out;
  transition: right .4s ease-out;
  right:-80px;
  bottom:80px;
  padding:2px 6px;
  background-color:#08a1fa;
  border-radius: 4px;
  cursor:pointer;
}
.backTop.active{
  right:6px;
}
.backTop:hover{
  background-color:#046ef8;
}



